Mock.mock(/\/radar/, 'get', () => {
  const data = {
    indicator: [
      {
        name: '',
        max: 6500,
      },
      {
        name: '',
        max: 16000,
      },
      {
        name: '',
        max: 30000,
      },
      {
        name: '',
        max: 38000,
      },
      {
        name: '',
        max: 52000,
      },
      {
        name: '',
        max: 25000,
      },
    ],
    series: [
      {
        data: [
          {
            value: [
              Random.natural(1000, 6500),
              Random.natural(3000, 16000),
              Random.natural(10000, 30000),
              Random.natural(15000, 38000),
              Random.natural(20000, 50000),
              Random.natural(10000, 25000),
            ],
            name: 'Allocated Budget',
          },
          {
            value: [
              Random.natural(1000, 6500),
              Random.natural(3000, 16000),
              Random.natural(10000, 30000),
              Random.natural(15000, 38000),
              Random.natural(20000, 50000),
              Random.natural(10000, 25000),
            ],
            name: 'Actual Spending',
          },
        ],
      },
    ],
  }
  return getResult(data)
})
